import React from 'react'

import { Link } from 'react-router-dom'



const NewsCard = ({ card }) => {
    return (
        <div className="news_card">
            <Link to={`news/${card.id}`}>
                <h2 className="news_card__title">{card.title}</h2>
            </Link>
            <div className="news_card__date_author">
                <span className="news_card__date_author__date">{card.date}</span>
                <span>
                    Автор:
                    <Link to={`trainers/${card.author.id}`}> {card.author.name}</Link>
                </span>
            </div>
            {!!card.img && <div>картинка</div>}
            <div className="news_card__desc">
                {card.desc}
            </div>
            <Link to={`news/${card.id}`} className="news_card__link">Читать полностью</Link>
        </div>
    )
}

export default NewsCard
