import React from 'react'

import { TimetableCard } from '../components'

import { map } from 'lodash'

import { Formik, Field, Form } from "formik";

const cards = [
    {
        age: 'Дети 7 - 9 лет',
        items: [
            {
                days: 'пн./ср.',
                time: '17:00 - 18:00'
            },
            {
                days: 'пт.',
                time: '17:00 - 18:00'
            }
        ]
    },
    {
        age: 'Дети 10 - 11 лет',
        items: [
            {
                days: 'пн./ср.',
                time: '17:00 - 18:00'
            },
            {
                days: 'пт.',
                time: '17:00 - 18:00'
            }
        ]
    },
    {
        age: 'Дети 12-14 лет',
        items: [
            {
                days: 'пн./ср.',
                time: '18:00 - 19:00'
            },
            {
                days: 'пт.',
                time: '18:00 - 19:00'
            }
        ]
    },
    {
        age: 'Дети 4 - 6 лет',
        items: [
            {
                days: 'пн./ср.',
                time: '19:00 - 20:00'
            },
            {
                days: 'вт./чт.',
                time: '19:00 - 20:00'
            }
        ]
    },
    {
        age: 'Дети 2.5 - 3.5 лет',
        items: [
            {
                days: 'понедельник',
                time: '18:00 - 18:45'
            },
            {
                days: 'суббота',
                time: '12:00 - 12:45'
            }
        ]
    },
]

const TimeTable = () => {
    const initialValues = { name: "", phone: "", age: "" }

    const onSubmit = (values) => {
        alert(`пробный урок \n Имя: ${values.name} \n Телефон: ${values.phone} \n Возраст: ${values.age}`)
    }

    return (
        <section className="timetable">
            <h1>Расписание</h1>
            <div className="timetable__cards_wrapper">
                {map(cards, (card, index) => <TimetableCard key={`${card}_${index}`} card={card} />)}
            </div>
            <div className="timetable__form_wrapper">

                <Formik
                    initialValues={initialValues}
                    onSubmit={onSubmit}
                >
                    <Form className="timetable__form">
                        <h2>Записаться на бесплатный урок</h2>
                        <div className="form__field">
                            <label>
                                Имя *
                                <Field type="text"
                                    className="timetable__form__input"
                                    placeholder="Ваше имя"
                                    name="name"
                                />
                            </label>
                        </div>
                        <div className="form__field">
                            <label>
                                Телефон *
                                <Field type="text"
                                    className="timetable__form__input"
                                    placeholder="Ваш телефон"
                                    name="phone"
                                />
                            </label>
                        </div>
                        <div className="form__field">
                            <label>
                                Возраст ребенка
                                <Field type="text"
                                    className="timetable__form__input"
                                    placeholder="Возраст ребенка"
                                    name="age"
                                />
                            </label>
                        </div>
                        <button className="form__btn">Отправить</button>
                    </Form>
                </Formik>
            </div>
        </section>
    )
}

export default TimeTable
