import React from 'react'

import { Link } from 'react-router-dom'

import { exit } from '../redux/actions/user'

const Header = ({ useSelector, dispatch }) => {
    const user = useSelector(({ user }) => user)

    const exitHandle = () => {
        dispatch(exit())
    }

    return (
        <section className="header">
            <ul className="header__main_menu">
                <Link to='/' className="header-link"><li className="header__main_menu-item">Главная</li></Link>

                <li className="submenu_general header__main_menu-item">
                    О нас
                    <ul className="submenu">
                        <Link to='/'><li className="submenu__item">Фото</li></Link>
                        <Link to='/trainers'><li className="submenu__item">Тренера</li></Link>
                        <Link to='/news'><li className="submenu__item">Новости</li></Link>
                    </ul>
                </li>

                <Link to='/timetable' className="header-link"><li className="header__main_menu-item">Расписание</li></Link>
                <Link to='/contacts' className="header-link"><li className="header__main_menu-item">Контакты</li></Link>
                <Link to='/admin' className="header-link"><li className="header__main_menu-item">Админ</li></Link>
            </ul>
            {!user.token
                ? <Link to='/auth' className="header-link">Вход</Link>
                : <span className="header-link" onClick={exitHandle}>Выход</span>
            }

        </section>
    )
}

export default Header
