import React from 'react'

import { Header } from './components'

import { Route, Switch } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios'
import { reactLocalStorage } from 'reactjs-localstorage';

import { Home, Auth, TimeTable, News, NewsFull, Contacts, Attendance } from './pages';
import { setToken } from './redux/actions/user'
// import { Admin } from './pages/admin';
const AdminComponent = React.lazy(() => import('./pages/admin/Admin'));

const App = () => {
  const dispatch = useDispatch();

  const token = useSelector(({ user }) => user.token)
  if (!token && reactLocalStorage.get('jwtToken')) {
    dispatch(setToken(reactLocalStorage.get('jwtToken', true)))
  }

  axios.defaults.headers.common['Authorization'] = 'JWT ' + token;
  return (
    <>
      <Header dispatch={dispatch} useSelector={useSelector}/>
      <section className="mainContainer">
        <Switch>
          <Route path="/" render={() => <Home />} exact />
          <Route path="/timetable" render={() => <TimeTable />} exact />
          <Route path="/news" render={() => <News />} exact />
          <Route path="/news/:id" render={({ match }) => <NewsFull match={match} />} exact />
          <Route path="/auth" render={() => <Auth />} exact />
          <Route path="/contacts" component={Contacts} />
          <Route path="/attendance" component={Attendance} />
          {/* <Route path="/admin" component={Admin} /> */}
          <Route path="/admin" render={() => (
            <React.Suspense fallback={<div>Loading...</div>}>
              <AdminComponent />
            </React.Suspense>
          )} />
        </Switch>

      </section>
    </>
  )
}

export default App
