import axios from 'axios';
import { reactLocalStorage } from 'reactjs-localstorage';

export const Types = {
    SET_TOKEN: 'USER@SET_TOKEN'
}

export const auth = (req_data) => (dispatch) => {
    axios.post('/auth', req_data).then(({ data }) => {
        reactLocalStorage.set('jwtToken', data['access_token'])
        dispatch(setToken(data['access_token']))
    })
}

export const exit = () => (dispatch) => {
    reactLocalStorage.remove('jwtToken')
    dispatch(setToken(null))
}

export const setToken = (items) => ({
    type: Types.SET_TOKEN,
    payload: items
})