import React from 'react'

// import { MapBlock } from '../components'

const MapBlock = React.lazy(() => import('../components/MapBlock'));

const Contacts = () => {
    return (
        <div className="testMap">
            <React.Suspense fallback={<div>Loading...</div>}>
                <MapBlock />
            </React.Suspense>
        </div>
    )
}

export default Contacts
