import React from 'react'

import { map, forEach } from 'lodash'
import axios from 'axios'

const Attendance = () => {
    const [attendance, setAttendance] = React.useState([])

    const inputChangeHandle = (id) => {
        setAttendance(prev => {
            forEach(prev.items, item => {
                if (item.dancer.id === id) {
                    item.present = !item.present
                }
            });
            return { ...prev }
        })
    }

    React.useEffect(() => {
        axios.get('api/attendance/get_by_lesson/1').then(({ data }) => setAttendance(data))
    }, [])
    return (
        <div className="attendance">
            <h1>Занятие {attendance.date}</h1>
            <table className="attendance__table">
                <thead>
                    <tr>
                        <th>Фамилия</th>
                        {/* <th>Посещаемость</th> */}
                        <th></th>
                    </tr>
                    {map(attendance.items, item => (
                        <tr className="attendance__table__row" key={item.dancer.id}>
                            <td className="attendance__table__name">
                                {item.dancer.name}
                            </td>
                            <td className="attendance__table__present">
                                <input type="checkbox" checked={item.present} onChange={() => inputChangeHandle(item.dancer.id)} />
                            </td>
                        </tr>
                    ))}
                </thead>
            </table>

        </div>
    )
}

export default Attendance
